<template>
    <div>
    <div class="login-box">
        <h1 style="text-align: center"></h1>
        <el-form :model="loginInfo" :rules="loginRules" >
            <el-form-item prop="username" class="form-control">
                <el-input v-model="loginInfo.username" placeholder=""></el-input>
            </el-form-item>
            <el-form-item prop="password" class="form-control" style="margin-bottom: 30px">
                <el-input v-model="loginInfo.password" placeholder=""  type="password"></el-input>
            </el-form-item>
            <div class="login-btn">
                <el-button type="primary" @click="submitLoginInfo"></el-button>
            </div>
            <a style="float: right;padding-top: 10px;font-size: 13px;color: #20a0ff;cursor: pointer" @click="showRegisterForm"></a>
        </el-form>
    </div>
        <el-dialog title="" v-model="visible" top="10%" size="tiny" >
            <div class="register-box">
                <el-form :model="registerInfo" :rules="registerRules"  label-position="top">
                    <el-form-item prop="username" class="form-control" label=" ">
                        <el-input v-model="registerInfo.username"></el-input>
                    </el-form-item>
                    <el-form-item prop="hardwareId" class="form-control" label="">
                        <el-input v-model="registerInfo.hardwareId"></el-input>
                    </el-form-item>
                    <el-form-item prop="email" class="form-control" label="">
                        <el-input v-model="registerInfo.email"></el-input>
                    </el-form-item>
                    <el-form-item prop="password" class="form-control" label="">
                        <el-input v-model="registerInfo.password" type="password"></el-input>
                    </el-form-item>
                    <div class="register-btn">
                        <el-button type="primary" @click="submitRegisterInfo" style="text-align: center;margin: 15px auto;width: 80%;position: relative;left: 10%"></el-button>
                    </div>
                </el-form>
            </div>
        </el-dialog>
    </div>
</template>

<script>
  import axios from 'axios';
  export default {
    data() {
      return {
        loginInfo: {
          username: '',
          password: '',
        },
        loginRules: {
          username: [
            {required: true, message: '', trigger: 'blur'},
          ],
          password: [
            {
              required: true, message: '', trigger: 'blur'},
          ]
        },
        registerInfo: {
          username: '',
          hardwareId: '',
          email: '',
          password: '',
        },
        registerRules: {
          username: [
            { required: true, message: '', trigger: 'blur'},
          ],
          hardwareId: [
            { required: true, message: '', trigger: 'blur'},
          ],
          email: [
            { required: true, message: '', trigger: 'blur', type: 'email'},
          ],
          password: [
            { required: true, message: '', trigger: 'blur'},
          ],
        },
        visible: false,
        loading: false,
      }
    },
    methods: {
      submitLoginInfo() {
        let url = this.$store.state.host + '/api/login';
        let userLoginInfo = this.loginInfo;
        axios.post(url, userLoginInfo).then(res => {
          if (res.data.status === 'success') {
            let info = {
              username: this.loginInfo.username,
              money: res.data.data.money,
            };
            this.$store.commit('initUserInfo', info);
            this.$router.push('/home/mainPage');
          } else {
            this.$message(
              {
                type: 'error',
                message: '',
              }
            );
          }
        });
      },
      showRegisterForm() {
        this.visible = true;
      },
      submitRegisterInfo() {
        let url = this.$store.state.host + '/api/register/addUsers';
        let userRegisterInfo = this.registerInfo;
        axios.post(url, userRegisterInfo).then(res => {
          if (res.data.status === 'success') {
            this.$message({
                type: 'success',
                message: '',
                duration: 3000,
              });
            setTimeout(() => {
              this.visible = false;
            }, 3500);
          } else {
            this.$message(
              {
                type: 'error',
                message: '',
              }
            )
          }
        });
      },
    }
  }
</script>

<style>

    body {
        background-color: #f1f2f7 !important;
    }

    .login-box {
        box-sizing: border-box;
        padding: 40px;

        box-shadow: 0 0 2px #ccc;
        border-radius: 10px ;
        max-width: 400px;
        margin: 10% auto;
        background-color: white;
        font-family: "Helvetica Neue",Helvetica,"PingFang SC","Hiragino Sans GB","Microsoft YaHei","",Arial,sans-serif;
    }
    .form-control {

        width: 100%;
        margin: 20px auto 20px auto;
    }
    .login-btn{
        margin: 0 auto;
        text-align: center;
    }

    .login-btn button {
        width: 100%;
    }

    .register-box .form-control {
        width: 80%;
        margin: 20px auto;
    }

    .el-form-item__error {
        position: relative;
        text-align: right;
        font-size: 14px;
        padding-top: 10px;
        padding-bottom: 0px;
    }





</style>


